import { Affix, Tabs, Timeline } from 'antd'
import React, { useEffect, useState } from 'react'
import { Flex, Box } from 'noui/Position'
import { isMobile } from 'noui/MediaQuery'
import { Paragraph, Header, Msg } from 'ui/Text'
import Portrait from 'ui/Portrait'
import TextWithImage from '../shared/components/TextWithImage'

import cover from '../shared/images/02_LATEST-STORY_Factions_ZHNT_InlineR_140702_0.jpg'
import logo from './shared/5e_Zhentarim_Symbol.png'

const GOALS = [
  '.',
  '   .',
  '      .',
  ' .',
]

const BELIEFS = [
  ' -  .   ,   .',
  ' -   .      ,    .',
  '      .',
]

const Zhentarim = ({history, location}) => {
  const [tab, setTab] = useState(location.hash.substring(1) || 'main')
  
  useEffect(() => {
    history.replace({
      path: location.path,
      hash: tab
    })
  }, [tab])
  
  return (
    <Box>
      <Tabs
        activeKey={tab}
        onChange={setTab}
        type="card"
        tabPosition={isMobile() ? 'top' : 'left'}
      >
        {/*Main*/}
        <Tabs.TabPane
          tab=""
          key="main"
        >
          <TextWithImage
            image={cover}
            text=""
          />
          
          <Box my={30}>
            <Paragraph fontSize="16px">
               -    ,   -      . 
              ,   ,       ,    
                 .
            </Paragraph>
          </Box>
          
          <Box my={10}>
            <Paragraph fontSize="16px">
                    ,        
              . ,     ,    , 
                     .
            </Paragraph>
          </Box>
        </Tabs.TabPane>
        
        <Tabs.TabPane
          tab="  "
          key="goals_and_beliefs"
        >
          <Box
            position="absolute"
            right={20}
            zIndex={10}
          >
            <Affix offsetTop={0}>
              <Portrait
                src={logo}
                alt=" 1"
              />
            </Affix>
          </Box>
          
          <Flex pb={10}>
            <Header fontSize={30} fontWeight="bold">
              :
            </Header>
          </Flex>
          
          <Timeline>
            {
              GOALS.map(goal =>
                <Timeline.Item>
                  <Msg fontSize={18}>
                    {goal}
                  </Msg>
                </Timeline.Item>
              )
            }
          </Timeline>
          
          <Flex pb={10}>
            <Header fontSize={30} fontWeight="bold">
              :
            </Header>
          </Flex>
          
          <Timeline>
            {
              BELIEFS.map(belief =>
                <Timeline.Item>
                  <Msg fontSize={18}>
                    {belief}
                  </Msg>
                </Timeline.Item>
              )
            }
          </Timeline>
          
          <Box my={10}>
            <Paragraph fontSize="16px">
                    
            </Paragraph>
          </Box>
        
        </Tabs.TabPane>
        
        <Tabs.TabPane
          tab=" "
          key="members"
          disabled
        >
        
        </Tabs.TabPane>
      </Tabs>
    </Box>
  )
}

export default Zhentarim
